import Vue from "vue";
import Router from "vue-router";
import SignIn from "./views/SignIn.vue";
import NotFound from "./views/NotFound.vue";
import Restaurants from './views/Restaurants.vue'
import store from './store'

Vue.use(Router);

const authorizeIsAdmin = (to, from, next) => {
  //  vuex  currentUser
  const currentUser = store.state.currentUser
  //  currentUser  admin  404 
  if (currentUser && !currentUser.isAdmin) {
    next('/404')
    return
  }
  next()
}


const router = new Router({
  linkExactActiveClass: 'active',
  routes: [{
      path: '/',
      name: 'root',
      redirect: '/signin'
    },
    {
      path: '/signin',
      name: 'sign-in',
      component: SignIn
    },
    {
      path: '/signup',
      name: 'sign-up',
      component: () => import('./views/SignUp.vue')
    },
    {
      path: '/restaurants',
      name: 'restaurants',
      component: Restaurants
    },
    {
      path: '/restaurants/feeds',
      name: 'restaurants-feeds',
      component: () => import('./views/RestaurantsFeeds.vue')
    },
    {
      path: '/restaurants/top',
      name: 'restaurants-top',
      component: () => import('./views/RestaurantsTop.vue')
    },
    {
      path: '/restaurants/:id',
      name: 'restaurant',
      component: () => import('./views/Restaurant.vue')
    },
    {
      path: '/restaurants/:id/dashboard',
      name: 'restaurant-dashboard',
      component: () => import('./views/RestaurantDashboard.vue')
    },
    {
      path: '/users/top',
      name: 'users-top',
      component: () => import('./views/UsersTop.vue')
    },
    {
      path: '/users/:id',
      name: 'user',
      component: () => import('./views/User.vue')
    },
    {
      path: '/admin',
      exact: true,
      redirect: 'admin/restaurants'
    },
    {
      path: '/admin/restaurants',
      name: 'admin-restaurants',
      component: () => import('./views/AdminRestaurants.vue'),
      beforeEnter: authorizeIsAdmin
    },
    {
      path: '/admin/restaurants/new',
      name: 'admin-restaurant-new',
      component: () => import('./views/AdminRestaurantNew.vue'),
      beforeEnter: authorizeIsAdmin
    },
    {
      path: '/admin/restaurants/:id/edit',
      name: 'admin-restaurant-edit',
      component: () => import('./views/AdminRestaurantEdit.vue'),
      beforeEnter: authorizeIsAdmin
    },
    {
      path: '/admin/restaurants/:id',
      name: 'admin-restaurant',
      component: () => import('./views/AdminRestaurant.vue'),
      beforeEnter: authorizeIsAdmin
    },
    {
      path: '/admin/users/:id/edit',
      name: 'admin-user-edit',
      component: () => import('./views/AdminUserEdit.vue'),
      beforeEnter: authorizeIsAdmin
    },
    {
      path: '/admin/categories',
      name: 'admin-categories',
      component: () => import('./views/AdminCategories.vue'),
      beforeEnter: authorizeIsAdmin
    },
    {
      path: '/admin/users',
      name: 'admin-users',
      component: () => import('./views/AdminUsers.vue'),
      beforeEnter: authorizeIsAdmin
    },
    {
      path: '*',
      name: 'not-found',
      component: NotFound
    },
  ]
});

//  beforeEach 
router.beforeEach(async (to, from, next) => {
  //  localStorage  token
  const tokenInLocalStorage = localStorage.getItem('token')
  //  vuex state  token
  const tokenInStorage = store.state.token;
  //  vuex state  isAuthenticated 
  let isAuthenticated = store.state.isAuthenticated;

  //  token 
  if (tokenInLocalStorage && tokenInLocalStorage !== tokenInStorage) {
    // 
    isAuthenticated = await store.dispatch('fetchCurrentUser')
  }

  // 
  const pathsWithoutAuthentication = ['sign-up'];
  if (pathsWithoutAuthentication.includes(to.name)) {
    next()
    return
  }

  //  token 
  if (!isAuthenticated && to.name !== 'sign-in') {
    next('/signin')
    return
  }

  //  token 
  if (isAuthenticated && to.name === 'sign-in') {
    next('/restaurants')
    return
  }

  next()
})

export default router